<template>
  <div style="height:100%;">
    <div class="flex-between" style="height:calc(100% - 54px);">
      <div style="overflow-y:auto;background-color:#f7f8fa;">
        <van-sidebar v-model="activeClassifyIndex" @change="_get_goods_list">
          <van-sidebar-item v-for="item,index in goodsClassifyList" :key="index" :title="item.cateName" />
        </van-sidebar>
      </div>
      <div style="width:calc(100% - 80px);background-color:#f9f9f9;overflow-y:auto;">
        <div class="list">
          <div class="item" v-for="(item,index) in goodsList" :key="index" @click="goDetail(item)">
            <div class="topImage">
            <img :src="$utils.dealImg(item.gimg)" alt />
            </div>
            <div class="bottomText">
              <p class="title">{{item.gname}}</p>
              <div class="labels">
                <div class="label" v-if="item.gfanj">送{{item.gfanj}}积分</div>
              </div>
              <div class="bottom">
                <div class="left">
                   <div class="price1">
                    <span class="label">优惠价</span>
                    <span class="font">¥</span>
                    <span class="num">{{item.goutPrice?item.goutPrice.toFixed(2):'0.00'}}</span>
                  </div>
                  <div class="price">
                    <span class="label">市场价</span>
                    <span class="font">¥</span>
                    <span class="num">
                      <del>{{item.gmarketPrice?item.gmarketPrice.toFixed(2):'0.00'}}</del>
                    </span>
                  </div>
                 </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getIndexMenu, getIndexGoodsList } from '@/api';
export default {
  data: ()=>{
    return {
      activeClassifyIndex: 0,
      goodsClassifyList: [],
      goodsList: [],
      keyWord: ''
    }
  },
  computed: {
    currCateCode: function(){
      return this.goodsClassifyList[this.activeClassifyIndex].cateCode;
    }
  },
  mounted(){
    document.title = '商品分类';
    this.loadData();
  },
  methods: {
    async loadData(){

      // 获取商城的二级分类
      let res = await getIndexMenu({
        cateCode: '825678A88DCD43528E3268E2F65A045D',
        cateLevel: 2
      })
      if (res.code == 0) {
        this.goodsClassifyList = res.data;
        this._get_goods_list();
      } else {
        this.$toast.fail(res.msg);
      }
    },
    goDetail(item) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: item.gcode, title: item.gname },
      });
    },
    _get_goods_list(){
      let dataObj = {
        cateCode: this.currCateCode,
        viewName: this.keyWord,
        pageNum: 1,
        pageSize: 10000,
      };
      getIndexGoodsList(dataObj).then(res=>{
        if(res.code === 0){
          console.log(res);
          if(res.data) this.goodsList = res.data.list;
        }else{
          this.$toast.fail(res.msg);
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .flex-between {
    display: flex;
    justify-content: space-between;
  }

  .list {
      padding: 10px;
      overflow: hidden;
      .item {
        border-bottom: 1px solid #eee;
        padding: 5px 0;
        align-items: flex-start;
        min-height: 120px;
        display: flex;
        .topImage {
          display: inline-block;
          img {
            width: 110px;
            height:100%;
            // height: calc(120px / 1.316);
          }
        }
        .bottomText {
          display: inline-block;
          .title {
            margin-left: 15px;
            padding-bottom:5px;
            font-size: 14px;
            line-height: 18px;
            max-height: 50px; 
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            color: #333;
            letter-spacing: 1px;
          }
          .labels {
            margin-left: 15px;
            margin-top: 10px;
            .label {
              display: inline-block;
              font-size: 11px;
              margin-right: 10px;
              padding: 3px 8px;
              border-radius: 5px;
              border: 1pt solid orange;;
              color: orange;
            }
          }
          .bottom {
            color: $base-color;
            font-size: 12px;
            line-height: 30px;
            width: 100%;
            display: flex;
            align-items: center;
            margin: 2px 0 0;
            padding-top: 10px;
            .left {
              margin-top: -6px;
              display: flex;
           
            .line{
              margin-top: 18px;
              //margin-left: 6px;
              width:93vw;
            }
            .price {
              display: inline-block;
              padding-left: 5px;
              
              .label {
                font-size: 10px;
                 color: #000000;
                background: #dae6fd;
                padding: 3px 5px;
                border-radius: 5px;
                margin-right: 5px;
                box-sizing: border-box;
                border:0;
              }
              .font {
                font-size: 10px;
                 color: #999;
                margin-right: 3px;
              }
              .num {
                font-weight: bold;
                font-size: 12px;
                color: #999;
              }
            }
            .price1 {
              display: inline-block;
              margin-left: 15px;
              .label {
                font-size: 10px;
               color: #f70707;
                border: 1pt solid #f70707;
                padding: 3px 5px;
                border-radius: 5px;
                margin-right: 5px;
                box-sizing: border-box;
              }
              .font {
                font-size: 12px;
               color: #f70707;
              }
              .num {
                font-weight: bold;
                font-size: 12px;
               color: #f70707;
              }
            } }
          }
        }
      }
    }
</style>